* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100%;
  background-color: #f8f9fa;
  background-image: url("world.83a651bf.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: 400px;
  margin-right: 400px;
  padding: 60px;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

h1 {
  text-align: center;
  color: #fff;
  text-shadow: 2px 2px 2px rgba(6, 6, 6, .1);
  margin-bottom: 20px;
}

#search-box, .country-list {
  width: 100%;
  text-align: center;
  background-color: #f8f9fa;
  border: none;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
  transition: box-shadow .3s;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

#search-box::-ms-input-placeholder {
  color: #999;
}

#search-box::placeholder {
  color: #999;
}

#search-box:focus {
  outline: none;
  box-shadow: 0 8px 12px rgba(0, 0, 0, .2);
}

.country-list {
  text-align: left;
  max-height: none;
  border-radius: 8px;
  padding: 0;
  list-style-type: none;
  overflow-y: auto;
}

.country-list li {
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 10px;
  transition: transform .3s;
  display: flex;
}

.country-list li span {
  flex-grow: 1;
}

.country-list li:hover {
  transform: translateY(-5px);
}

.country-list img {
  border: 2px solid #000;
  border-radius: 4px;
  margin-left: 20px;
  margin-right: 15px;
}

.country-info {
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.country-info h1 {
  color: #000;
  margin-bottom: 10px;
  font-size: 24px;
}

.country-info img {
  border: 2px solid #000;
  border-radius: 4px;
  margin: 0 auto 10px;
  display: block;
}

.country-info p {
  text-align: center;
  margin-bottom: 10px;
}

@media screen and (max-width: 600px) {
  #search-box {
    font-size: 14px;
  }
}

